import { Component, ViewEncapsulation } from '@angular/core';
import { NavigationEnd, Router } from '@angular/router';
import { ScullyRoutesService } from '@scullyio/ng-lib';
import { filter, switchMap } from 'rxjs/operators';

@Component({
  selector: 'scheibentoenung-schmieder-staticpages',
  templateUrl: './staticpages.component.html',
  styleUrls: ['./staticpages.component.css'],
  preserveWhitespaces: true,
  encapsulation: ViewEncapsulation.Emulated,
})
export class StaticpagesComponent {
  current$ = this.router.events.pipe(
    filter((event) => event instanceof NavigationEnd),
    switchMap((_) => this.scully.getCurrent())
  );

  constructor(private router: Router, private scully: ScullyRoutesService) {}
}
